import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import ARScene from '@/components/ARScene'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ARScene',
      component: ARScene
    }
  ]
})



